import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import foto from "../assets/img/foto.jpg";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(100);
  const [index, setIndex] = useState(1);
  const toRotate = [ "Computer Scientist", "Software Engineer", "Clarinet Player", "Researcher at HASLab" ];
  const period = 1000;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta * 0.9);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex(prevIndex => prevIndex + 1);
      setDelta(100);
    }
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="align-items-center">
          <Col xs={12} md={6} xl={7}>
            <div className="">
              <span className="tagline">Welcome to my Portfolio</span>
              <h1>{`Hi! I'm Andr Lucena`} <span className="txt-rotate" dataPeriod="1000" data-rotate='[ "Web Developer", "Web Designer", "UI/UX Designer" ]'><span className="wrap">{text}</span></span></h1>
                <p>23 year old Bachelor in Computer Science, currently finishing Bachelor's in Clarinet and Master's in Software Engineering, all at Universidade do Minho, Braga.</p>
            </div>
          </Col>
          <Col xs={12} md={6} xl={5}>
              <div className="">
                <img src={foto} alt="Header Img"/>
              </div>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
